
import App from '@/tongji/App.vue';
import router from '@/tongji/router';
import { createApp } from 'vue';

import '@/assets/font/iconfont.css';
import '@/assets/font/iconfont.js';
// import '@/assets/font/iconfont1.css';
import '@/lib/css/main.scss';
import init from '@/lib/js/init.js';
import ElementPlus from "element-plus";
import zhCn from "element-plus/dist/locale/zh-cn.mjs";
import 'element-plus/theme-chalk/el-message-box.css';
import Vue3ColorPicker from "vue3-colorpicker";
import "vue3-colorpicker/style.css";
import '@/style/gloabe.scss';
const app = createApp(App)

//按需进行请求挂载
init(app,{
    "protocol": "",
    "hostname": "",
    "port": 0
}).then(store=>{
    app.use(store).use(router).use(Vue3ColorPicker).use(ElementPlus,{locale:zhCn}).mount('#app_tongji')
}).catch(err=>{
    console.clear()
    console.dir(err)
})
